
.grid-container {
  display: grid;
  grid-template-columns: 1fr 800px 50px 1fr ;
  grid-template-rows: 30px 275px 40px 3fr;
  gap: 10px 1px;
  grid-template-areas:
    ". . . ."
    ". uploadArea . ."
    ". widthInfoArea . ."
    ". imgParent lengthInfo .";
  

    background: -webkit-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: -moz-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: -o-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: linear-gradient(to bottom right, #F3F6F1 0%, #D9DED6 100%);
    font-family: 'Roboto', sans-serif;
    color: #31345A;
}


.uploadArea {
  display: grid;
  grid-template-columns: 4fr 1fr 2fr .5fr;
  grid-template-rows: 100px 75px 100px;
  gap: 1px 20px;
  grid-template-areas:
    "logo logo . ."
    "upload inputTile inputColor hexButton "
    "hex hex hex hex";
  grid-area: uploadArea;
}

.tileCount{
  display: grid;
  grid-template-columns: 23px 1fr;
  justify-content: left;
  align-items: center;
}

input[type="color"] {
	-webkit-appearance: none;
	border: none;
	width: 15px;
  height: 15px;
  
}
input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}
input[type="color"]::-webkit-color-swatch {
  border: none;
  border-radius: 2px;
}

.logo { 
  display: grid; 
  grid-template-columns: 190px 400px;
  grid-template-areas:
    "logoTitle logoDescription";
  grid-area: logo;}
 
.logoTitle{
  display: grid;
  justify-content: left;
  grid-area: logoTitle;
  align-items: center;
}

.logoDescription{
    display: grid;
    justify-content: left;
    align-items: center;
    grid-area: logoDescription;
}
 .hexButton { 
  grid-area: hexButton;
  display: grid; 
  justify-content: right; 
  align-items: center;
}

 .upload { 
  grid-area: upload;
  display: grid; 
  justify-content: left; 
  align-items: center;
}

.hex { 
  display: grid;
  grid-area: hex; 
  justify-content: right; 
}

#hexChild {
  min-width: 800px;
  justify-content: left; 
}

.inputColor { 
  grid-area: inputColor;
  display: grid; 
  justify-content: right;
}

.inputTile { 
  grid-area: inputTile;
  display: grid; 
  justify-content: right; 
}

.total { 
  display: grid;
  grid-area: total;
  justify-content: right; 
}

.widthInfoArea{ 
  display: grid; 
  grid-template-columns: 1fr 1fr;
  grid-area: widthInfoArea;
  grid-template-areas:
    "loadingInfo widthInfo";  
  /* justify-content: right; */
  align-content: end; 
}
.widthInfo{
  display: grid; 
  grid-area: widthInfo;
  justify-content: right;
}

.loadingInfo{
  display: grid; 
  grid-area: loadingInfo;
  justify-content: left;
}

.lengthInfo { 
  display: grid;
  grid-template-columns: 1fr;
  /* grid-template-rows: 1fr; */
  gap: 1px 1px;
  grid-template-areas:
    "length";
  grid-area: lengthInfo;
  justify-content: center;
}

.affiliate { display: grid; 
  grid-area: affiliate; 
  align-content: center; 
  justify-content: right; }

.link{
  color:#EB2F7D;
  text-decoration: none;
}


.length {
   display: grid;
   grid-area: length;
   justify-content: center;
   }
   #rotate-text{
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
   }

.imgParent {
  grid-area: imgParent;
  width: 800px;
}
canvas,img {
  display: block;
  width: 100%;
  height: auto;
}


